<template>
  <div class="login-container">
    <div class="login_header">
      <img src="" alt="此处为logo" class="logo">
      <span>J2EE</span>
    </div>
    <el-form :model="ruleForm2" :rules="rules2" status-icon ref="ruleForm2" label-position="left" label-width="0px"
      class="demo-ruleForm login-page">
      <el-form-item prop="username">
        <el-input type="text" v-model="ruleForm2.username" auto-complete="off" placeholder="用户名 Username"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="ruleForm2.password" auto-complete="off" placeholder="密码 Password"></el-input>
      </el-form-item>
      <el-form-item prop="yzm" class="yzm_container">
        <el-input type="text" v-model="ruleForm2.yzm" auto-complete="off" placeholder="请输入验证码" style="width:120px;"></el-input>
        <div class="yzm_img-container">
          <img src="验证码图片" alt="">
        </div>
      </el-form-item>
      <el-form-item style="width:100%;">
        <el-button type="success" round style="width:100%;" @click="handleSubmit" :loading="logining">登录</el-button>
      </el-form-item>
    </el-form>
    <div class="login_title">
      <dl>
        <dt class="">实时在线人数:</dt>
        <dd class="number">123456</dd>
        <dt class="">现有图片数:</dt>
        <dd class="number">123456</dd>
      </dl>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        logining: false,
        ruleForm2: {
          username: "",
          password: "",
          yzm: ""
        },
        rules2: {
          username: [{
            required: true,
            message: "please enter your account",
            trigger: "blur"
          }],
          password: [{
            required: true,
            message: "enter your password",
            trigger: "blur"
          }]
        },
        checked: false
      };
    },
    methods: {
      handleSubmit() {
        this.$refs.ruleForm2.validate(valid => {
          if (valid) {
            this.logining = true;
            sessionStorage.setItem("user", this.ruleForm2.username);
            this.$ajax({
              method: 'post',
              url: '/login',
              data: {
                name: 'qqq',
                username: this.ruleForm2.username,
                password: this.ruleForm2.password
              }
            }).then(function(res) {
              console.log(res);
              this.$router.push({
                path: "/"
              });
            })
            
            // if (
            //   this.ruleForm2.username === "admin" &&
            //   this.ruleForm2.password === "123456"
            // ) {
            //   this.logining = false;
            //   sessionStorage.setItem("user", this.ruleForm2.username);
            //   this.$router.push({
            //     path: "/"
            //   });
            // } else {
            //   this.logining = false;
            //   this.$alert("username or password wrong!", "info", {
            //     confirmButtonText: "ok"
            //   });
            // }
          } else {
            console.log("error submit!");
            return false;
          }
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
  .login-container {
    width: 100%;
    height: 100%;
    background-color: blue;
  }

  .login_header {
    padding: 0 10px;
    height: 50px;
    line-height: 50px;
    background-color: rgba(56, 33, 105, 0.4);

    img.logo {
      display: inline-block;
      margin-right: 10px;
    }
  }

  .login-page {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    margin-top: 50px;
    margin-right: 50px;
    margin-left: auto;
    width: 200px;
    padding: 35px 35px 15px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    background-color: rgba(44, 21, 93, 0.4);
  }

  label.el-checkbox.rememberme {
    margin: 0px 0px 15px;
    text-align: left;
  }

  .yzm_container {
    /deep/ .el-form-item__content {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
    }

    .el-input {
      margin-right: 10px;
    }
  }

  .yzm_img-container {
    width: 60px;
    background: #fff;
  }

  .login_title {
    position: absolute;
    left: 60px;
    bottom: 40px;
    color: #fff;
    font-size: 28px;

    dt {
      font-size: 24px;
      margin-bottom: 10px;
    }

    dd {
      font-weight: bolder;
      margin-left: 100px;
    }
  }
</style>



