import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/loginpage'
import Container from '@/views/container'
import User from '@/views/usermanage'
import Image from '@/views/imagemanage'

Vue.use(Router)

export default new Router({
  base: '/J2EE/',
  routes: [{
    path: '/loginpage',
    name: 'Loginpage',
    component: Login
  }, {
    path: '/',
    redirect: '/user',
    name: 'Container',
    component: Container,
    children: [{
        path: '/user',
        name: "用户管理",
        component: User

      },
      {
        path: '/image',
        name: '图片库管理',
        component: Image
      }
    ]
  }]
})



