<template>
  <div class="user">
    <el-button style="backgroundColor:#58D395;color:#000;margin-bottom:20px;" @click="handleInsert">新建用户</el-button>
    <div class="user_search">
      <el-input placeholder="查找用户" suffix-icon="el-icon-search" v-model="input1">
      </el-input>
      <el-checkbox v-model="checked">显示管理员账户</el-checkbox>
    </div>
    <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" height="75%" style="width: 100%" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column prop="id" label="用户ID" width="120"></el-table-column>
      <el-table-column prop="name" label="用户名" width="120">
      </el-table-column>
      <el-table-column prop="qx" label="权限">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="新增用户" :visible.sync="userVisible">
      <el-form :model="insertForm" label-width="90px">
        <el-form-item label="用户名">
          <el-input v-model="insertForm.name" placeholder="请输入用户名" autocomplete="off" style="width:217px;"></el-input>
        </el-form-item>
        <el-form-item label="权限">
          <el-select v-model="insertForm.qx" placeholder="请选择权限">
            <el-option label="管理员" value="1"></el-option>
            <el-option label="普通用户" value="2"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel">取 消</el-button>
        <el-button type="primary" @click="handleInsertSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [
        {
          id: "000001",
          name: "abcdefg",
          qx: "管理员"
        }
      ],
      userVisible: false,
      insertForm: {
        name: "",
        qx: ""
      }
    };
  },
  methods: {
    handleInsert() {
      this.userVisible = true;
    },
    handleCancel() {
      this.userVisible = false;
    },
    handleInsertSave() {
      this.userVisible = false;
    },
    handleDelete() {
      this.$confirm("此操作将永久删除该条数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.user {
  height: calc(100% - 30px);
  padding: 15px;
  background-color: #e3e3e3;
  box-sizing: border-box;
}
.user .user_search {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin-bottom: 20px;
}
.user .user_search .el-input {
  margin-right: 10px;
}
</style>




