<template>
  <div class="image">
    <el-form ref="form" :model="searchForm" inline label-width="80px">
      <el-form-item label="国家">
        <el-select v-model="searchForm.country" placeholder="请选择活动区域" style="width:130px">
          <el-option label="中国" value="1"></el-option>
          <el-option label="美国" value="2"></el-option>
          <el-option label="英国" value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="分辨率">
        <el-select v-model="searchForm.px" placeholder="请选择活动区域" style="width:130px">
          <el-option label="1024×768" value="1"></el-option>
          <el-option label="800×600" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="添加时间">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="选择开始日期" v-model="searchForm.date1" style="width:150px"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-date-picker placeholder="选择结束时间" v-model="searchForm.date2" style="width:150px"></el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item style="margin-left:10px">
        <el-button type="primary">搜索</el-button>
      </el-form-item>
    </el-form>
    <div class="image_content">
      <el-row :gutter="20">
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <img src="" alt="">
            <span>图片名字</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <img src="" alt="">
            <span>图片名字</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <img src="" alt="">
            <span>图片名字</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <img src="" alt="">
            <span>图片名字</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <img src="" alt="">
            <span>图片名字</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <img src="" alt="">
            <span>图片名字</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <img src="" alt="">
            <span>图片名字</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <img src="" alt="">
            <span>图片名字</span>
          </div>
        </el-col>
      </el-row>
      <div class="page_container">
        <el-pagination background layout="prev, pager, next" :total="1000">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      searchForm: {
        country: "",
        px: "",
        date1: "",
        date2: ""
      }
    };
  }
};
</script>
<style lang="scss" scoped>
.image {
  height: calc(100% - 30px);
  padding: 15px;
  background-color: #e3e3e3;
  box-sizing: border-box;
  .image_content {
    height: calc(100% - 104px);
    background-color: #fff;
    padding: 20px 130px;
  }
  .image_content {
    .bg-purple {
      position: relative;
      height: 150px;
      border-radius: 20px;
      background-color: #4a2086;
      overflow: hidden;
      margin-bottom: 20px;
      img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }
      span {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 999;
        width: 100%;
        height: 30px;
        line-height: 30px;
        text-align: center;
        color: #fff;
        background-color: rgba(0, 0, 0, 0.4);
      }
    }
    .page_container {
      margin-top: 20px;
      display: flex;
      flex-flow: row nowrap;
      justify-content: center;
    }
  }
}
</style>




