<template>
  <div class="app">
    <el-container style="height:100%;">
      <el-header class="app-header">
        <div class="app_header">
          <img src="" alt="" class="logo">
          <span>平台名字平台名字</span>
        </div>
        <div class="app-header-userinfo">
          <el-dropdown trigger="hover" :hide-on-click="false">
            <span class="el-dropdown-link">
              <img src="" /> {{ username }}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>我的消息</el-dropdown-item>
              <el-dropdown-item>设置</el-dropdown-item>
              <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>
      <el-container>
        <el-aside class="app-side app-side-left" style="width:200px;">
          <div>
            <el-menu router :default-active="$route.path" class="el-menu-vertical-demo">
              <el-menu-item index="/user">用户管理</el-menu-item>
              <el-menu-item index="/image">图片库管理</el-menu-item>
            </el-menu>
          </div>
        </el-aside>

        <el-main class="app-body">
          <div class="title">{{$route.name}}</div>
          <template>
            <router-view/>
          </template>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "Container",
  data() {
    return {
      username: "",
      isCollapse: false
    };
  },
  methods: {
    toggleSideBar() {
      this.isCollapse = !this.isCollapse;
    },
    logout: function() {
      this.$confirm("确认退出?", "提示", {})
        .then(() => {
          sessionStorage.removeItem("user");
          this.$router.push("/loginpage");
        })
        .catch(() => {});
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  mounted: function() {
    let user = sessionStorage.getItem("user");
    if (user) {
      this.username = user;
    }
  }
};
</script>

<style lang="scss">
.app {
  height: 100%;
}
.app-header {
  height: 30px;
  padding: 0 10px;
  line-height: 60px;
  background: #4a2086;
}
.app_header {
  color: #fff;
  display: inline-block;
}
.app-header::after {
  content: "";
  display: block;
  height: 0;
  clear: both;
}
.app-header-userinfo {
  float: right;
}
.app-header-userinfo {
  color: #fff;
}
.app-aside {
  background-color: #5d3895;
}
.app {
  .el-container {
    height: calc(100% - 60px);
  }
  .el-menu {
    background-color: #5d3895;
  }
  .app-side {
    background-color: #5d3895;
  }
  /deep/ .el-dropdown {
    color: #fff;
  }
  /deep/ .el-menu-item {
    margin-top: 20px;
    font-size: 20px;
    color: #ddd;
  }
  /deep/ .el-menu-item:hover,
  /deep/ .el-menu-item:focus {
    background-color: #5d3895;
    color: #fff;
  }
  .el-main {
    padding: 0;
  }
  .title {
    color: #412772;
    padding-left: 30px;
    line-height: 30px;
  }
}
</style>



